import React from "react";
import { Route, Switch } from "react-router-dom";
import LoggedInLayout from "../Layout/LoggedInLayout";
import LoggedInHome from "../pages/LoggedInPages/LoggedInHome/LoggedInHome";
import Order from "../pages/LoggedInPages/Order/Order";
import AddressMap from "../pages/LoggedInPages/Profile/AddressMap";
import Profile from "../pages/LoggedInPages/Profile/Profile";
import ProfileAddress from "../pages/LoggedInPages/Profile/ProfileAddress";

export const privateRouteData = [
	{
		path: "/home/order",
		exact: true,
		component: Order,
	},
	{
		path: "/home/category/:cat/:subCat",
		exact: true,
		component: LoggedInHome,
	},

	{
		path: "/home/profile",
		exact: true,
		component: Profile,
	},

	{
		path: "/home/profile/address",
		exact: true,
		component: ProfileAddress,
	},

	{
		path: "/home/profile/address/map",
		exact: true,
		component: AddressMap,
	},
];

const PrivateRoutes = () => {
	return (
		<LoggedInLayout>
			<Switch>
				{privateRouteData.map((each) => {
					return <Route path={each.path} exact={each.exact} component={each.component} />;
				})}
			</Switch>
		</LoggedInLayout>
	);
};

export default PrivateRoutes;
