import React, { useState } from "react";
import AddProductModal from "./AddProductModal";

const GridProduct = ({ data }) => {
	const [isModalOpen, setIsModalOpen] = useState(false);

	const closeModal = () => {
		setIsModalOpen(false);
	};

	const openModal = () => {
		setIsModalOpen(true);
	};

	const { weight, name, price } = data;
	return (
		<div className="tw-bg-white tw-relative tw-p-3">
			<AddProductModal show={isModalOpen} handleClose={closeModal} />
			<div
				style={{ top: "5%", right: "5%" }}
				className="tw-h-5 tw-w-5 tw-shadow tw-absolute tw-cursor-pointer tw-rounded  tw-flex tw-items-center tw-justify-center"
			>
				<img
					onClick={openModal}
					src={require("../../../../assets/icons/add-icon.svg").default}
					className="tw-w-3"
					alt=""
				/>
			</div>
			<img
				src={require("../../../../assets/images/three-crowns.svg").default}
				className="tw-h-32 tw-block tw-mx-auto"
				alt=""
			/>
			<div className="tw-text-center">
				<p className="tw-text-jt-blue jt-glb tw-mt-3">₦{price}</p>
				<p className="tw-text-jt-black tw-h-10 tw-overflow-hidden jt-gln tw-mt-3">{name}</p>
				<p className="tw-text-gray-400 jt-gln tw-mt-3">{weight}g</p>
			</div>
		</div>
	);
};

export default GridProduct;
